.header-menu ul {
  width: 1280px;
  height: auto;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 30px 0;
  display: flex;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-menu ul {
    display: none;
  }
}

.header-menu ul, .header-menu ul li {
  list-style-type: none;
}

.header-menu ul li {
  margin-right: 40px;
  transition: all .3s ease-in;
  position: relative;
}

.header-menu ul li:not(:last-child) {
  margin-right: 40px;
}

.header-menu ul li a {
  color: #5c5c5c;
  text-decoration: none;
}

.header-menu ul li ul {
  width: 500px;
  visibility: hidden;
  opacity: 0;
  flex-wrap: wrap;
  transition: opacity .6s ease-in;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.header-menu ul li:hover ul {
  visibility: visible;
  opacity: 1;
}

.header-menu ul li ul li {
  width: 250px;
  padding: 10px 0;
}

.header-menu ul li .arrow-down {
  width: 0;
  height: 0;
  border-top: 5px solid #111;
  border-left: 5px solid rgba(0, 0, 0, 0);
  border-right: 5px solid rgba(0, 0, 0, 0);
  transition-property: transform;
  transition-duration: .8s;
  position: absolute;
  top: 7px;
  right: -15px;
}

.header-menu ul li:hover .arrow-down {
  transform: rotate(180deg);
}

.three, .mobile-menu {
  display: none;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .three, .mobile-menu {
    display: block;
  }
}

.visible {
  transition: all .5s;
  opacity: 1 !important;
}

.mobile-menu {
  height: 100vh;
  width: 100%;
  opacity: 0;
  background: #000;
  transition: all .5s;
  position: absolute;
  top: 0;
  right: 0;
}

.mobile-menu .container-mobile {
  color: #fff;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.mobile-menu ul li {
  padding: 10px;
  font-size: 30px;
  list-style-type: none;
}

.mobile-menu ul li a {
  color: #efefef;
}

.hamburger {
  z-index: 2;
  background-color: #111;
  padding: 5px;
  position: absolute;
  top: 20px;
  right: 20px;
}

.hamburger:hover {
  cursor: pointer;
}

.hamburger .line {
  width: 30px;
  height: 5px;
  -o-transition: all .3s ease-in-out;
  background-color: #fff;
  margin: 5px;
  transition: all .3s ease-in-out;
  display: block;
}

#hamburger-1.is-active .line:nth-child(2) {
  opacity: 0;
}

#hamburger-1.is-active .line:nth-child(1) {
  transform: translateY(10px)rotate(45deg);
}

#hamburger-1.is-active .line:nth-child(3) {
  transform: translateY(-10px)rotate(-45deg);
}

.footer-menu {
  justify-content: flex-start;
